import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      searchinput: "",
      orderlist: ["ascending", "descending"],
      tableData: [],
      total: 0,
      fileList: [],
      rules: {},
      form: {
        act_name: "",
        type_id: 1,
        status_id: "",
        act_info: "",
        start_time: "",
        end_time: "",
        auth: "root"
      },
      options: [
        {
          value: "选项1",
          label: "VIP"
        },
        {
          value: "选项2",
          label: "全民代理"
        },
        {
          value: "选项3",
          label: "客服"
        },
        {
          value: "选项4",
          label: "兑换"
        },
        {
          value: "选项5",
          label: "充值"
        },
        {
          value: "选项6",
          label: "活动"
        },
        {
          value: "选项7",
          label: "绑定手机"
        }
      ],
      options2: [
        {
          value: "选项8",
          label: "左下"
        },
        {
          value: "选项9",
          label: "中下"
        },
        {
          value: "选项10",
          label: "右下"
        }
      ],
      value: "",
      total: 0,
      currentPage: 1,
      limit: 10,
      visible: false,
      title: "添加系统公告",
      selectList: [],
      loading: false
    };
  },
  created() {
    this.initdata({
      page: this.currentPage,
      limit: this.limit,
      title: this.searchinput
    });
  },
  methods: {
    //多选删除
    del() {
      //勾选需要删除的项目批量删除
      if (this.selectList.length != 0) {
        this.$confirm("确认删除吗?", "信息", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(async () => {
            let str = this.selectList.join();
            // console.log(str);
            let { data } = await this.$http.HallFunConfig.DeleteActivityList({
              id_list: `(${str})`
            });
            console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.initdata({
                page: this.currentPage,
                limit: this.limit,
                title: this.searchinput
              });
            }

            this.$message({
              type: "success",
              message: "删除成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else {
        this.$message("请选择需要删除的数据");
      }
    },

    //添加
    add() {
      this.editForm("添加活动", true, {});
      this.$refs.formName.resetFields();
    },

    //发送到服务器配置
    send() {
      this.$confirm("确认发送吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(async () => {
        this.loading = true;
        let { data } = await this.$http.HallFunConfig.PostTableConfig({
          type_id: 7
        });
        // console.log(data);
        if (data.code === 1 && data.data) {
          this.loading = false;
          this.$message({
            type: "success",
            message: data.msg
          });
        }
       
      });
    },

    //搜索
    search() {
      if (this.searchinput === "") {
        this.$message({
          type: "warning",
          message: "请输入你要搜索的活动名称!"
        });
      }
      this.currentPage = 1;
      this.limit = 10;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //表格多选
    handleSelectionChange(sel) {
      let idList = sel.map(item => item.id);
      // console.log(idList);

      this.selectList = idList;
    },

    //页容量发生变化
    handleSizeChange(num) {
      // console.log(num);
      this.currentPage = 1;
      this.limit = num;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },

    //页码变化
    handleCurrentChange(pagenum) {
      // console.log(pagenum);

      this.currentPage = pagenum;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        title: this.searchinput
      });
    },
    handlePreview() {},
    handleRemove() {},
    onSuccess(response, file, fileList) {
      console.log(response, file, fileList);
    },

    //表格编辑
    handleEdit(row, formName) {
      // this.visiblity = true;
      // this.title = "更新系统公告";
      this.editForm("更新活动", true, {});
      this.$refs.formName.resetFields();
    },

    //表格删除
    handleDelete(x, row) {
      console.log(x, row.id);

      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async () => {
          let { data } = await this.$http.HallFunConfig.DeleteActivityList({
            id: row.id
          });
          if (data.code === 1 && data.msg === "ok") {
            this.initdata({
              page: this.currentPage,
              limit: this.limit,
              title: this.searchinput
            });
          }
          console.log(data);

          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //表单提交
    onSubmit() {
      //表单验证
      this.$refs.form.validate(async valid => {
        if (valid) {
          //1.将form数据传递到paylist,新增到table中先显示一个dom
          console.log(this.form);
          // this.form.act_info =
          let { data } = await this.$http.HallFunConfig.PostActivityList(
            this.form
          );
          console.log(data);

          //2.发送请求追加数据到后台-------------------------------------------------------------

          // console.log("发送请求");

          //3.关闭新增的弹框
          this.editForm("添加活动", false, {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //表单返回
    back() {
      this.editForm("添加活动", false, {});
    },

    editForm(title, visible, form) {
      this.title = title;
      this.visible = visible;
      this.form = form;
    },

    formateData(res) {
      res.forEach(item => {
        item.act_status = item.act_status === 1 ? "待上线" : "生效中";

        switch (item.act_type) {
          case 1:
            item.act_type = "普通";
            break;
          case 2:
            item.act_type = "跳转";
            break;
          case 3:
            item.act_type = "跳转网页";
            break;
          case 4:
            item.act_type = "任务";
            break;
          case 5:
            item.act_type = "首充活动";
            break;
          case 6:
            item.act_type = "排行榜活动";
            break;
          case 7:
            item.act_type = "绑定手机活动";
            break;

          default:
            break;
        }
      });
      return res;
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetActivityList(params);
      // console.log(data);
      let localdata = this.formateData(DeepData(data.data));
      this.tableData = localdata;
      this.total = data.total;
      // console.log(localdata);
      // console.log(data);
    }
  }
};



